import searchView from '@/components/search/search';
import shareView from '@/components/share/share';
export default {
	data() {
		return {
			screenHeight: 0,
			lists: [],
			items: [],
			index: 0,
			isShow: 0
		}
	},
	components:{searchView,shareView},
	onLoad(e) {
		uni.getSystemInfo({
			success: (o) => {
				console.log(o)
				this.screenHeight = o.windowHeight;
			}
		});
		this.getData();
	},
	methods: {
		getData (e) {
			var t = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: t.$serverUrl + '/class',
				data: { 
					appkey: t.$appkey,
				},
				success: (ret) => {
					const data = ret.data.result;
					t.lists = data;
					t.items = data[t.index].list;
					uni.hideLoading();
					t.isShow = !t.isShow;
				}
			});
		},
		getItem (item,i) {
			this.index = i;
			this.items = item.list;
		},
		goSearch (item) {
			uni.navigateTo({
				url: '../search/search?value=' + item.name + '&classid=' + item.classid
			})
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			return {
				title: '印记菜谱',
				desc: "学菜谱,就用印记菜谱,厨房小能手就是你~~~",
				success: res => {},
				fail: err => {}
			}
		}
		return {
			title: '印记菜谱',
			desc: "学菜谱,就用印记菜谱,厨房小能手就是你~~~",
			success: res => {},
			fail: err => {}
		}
	}
}
